@import "./styles/partials/variables";

.app {
	background-color: $bg-primary;
	min-height: 100vh;
	height: 100%;
}
.container {
	max-width: 1024px;
	margin: 0 auto;
	flex: 1;
}
@media (max-width: 1024px) {
	.container {
		max-width: 768px;
	}
}
.todo {
	display: flex;
	justify-content: space-around;
	height: 100%;
	padding-top: 2rem;
}
.todo_list {
    height: fit-content;
    position: sticky;
    top: 0;
	margin: 1rem;
}
@media (max-width: 500px) {
	.todo_list {
		height: fit-content;
    	position: relative;
	}
	.todo {
		flex-wrap: wrap;
	}
}
.add_btn {
	cursor: pointer;
	transition: 0.5s;
	&:hover {
		transform: scale(1.2);
	}
}
.todo_task {
	background-color: $bg-white;
	min-width: 240px;
	max-width: 360px;
	width: 100%;
	margin: 1rem;
}
.footer {
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translate(-50%, -50%);
	color: $color-light;
	font-size: 14px;
	min-width: 300px;
	text-align: center;
}
